"use client";
import { useReservation } from "@/app/_components/ReservationContext";
import User from "./User";
import ReservationForm from "./LoginMessage";
import { getUser } from "../_lib/apiService";
import { Suspense, useEffect } from "react";
import Spinner from "./Spinner";

function Account() {
  const { user, Setuser } = useReservation();
  useEffect(
    function () {
      async function getCurrentUser() {
        try {
          const data = await getUser();
          Setuser(data);
        } catch (err) {
          console.log(err);
        }
      }
      getCurrentUser();
    },
    [Setuser]
  );
  return (
    <Suspense fallback={<Spinner />} key={user}>
      {user.firstName ? <User user={user} /> : <ReservationForm />}
    </Suspense>
  );
}

export default Account;
